/* eslint-disable */
import './App.css';
import MainScreen from "./Screens/Main";
import {Colors} from "./Common/Constants";
import {useEffect, useState} from "react";
import {loadEdgesVertexToStore} from "./Common/Storage";
import {Loader} from "./Components/Loader";

const App = () => {
  const [isLoading, setLoading] = useState(true);
  const [searchText, setSearchText] = useState('');

  useEffect(() => {
    document.title = "P2P | OGNI"
  }, []);

  useEffect(() => {
    loadEdgesVertexToStore(searchText)
      .then(() => console.log("loadEdgesVertexToStore.then invoked"))
      .finally(() => setLoading(false));
  }, []);

  const onSearch = (tx) => {
    console.log('search', tx);
    setSearchText(tx)
    setLoading(true)
    loadEdgesVertexToStore(tx)
      .then(() => console.log("loadEdgesVertexToStore.then invoked"))
      .finally(() => setLoading(false));
  }

  return (
    <div style={{backgroundColor: Colors.Blue, width: "100%", height: "100vh"}}>
      {isLoading ?
        (<div style={{position: 'absolute', left: '40%', top: '40%'}}>
          <Loader />
        </div>) :
        <MainScreen onSearch={onSearch} searchText={searchText} />}
    </div>
  );
}

export default App;
