/* eslint-disable */
import React, {useState} from 'react';
import {Colors} from "../../Common/Constants";

const DetailContainer = ({children, name, clsName, value}) => {
  return (
    <div style={{
      marginTop: 35,
      minWidth: 130,
      backgroundColor: Colors.DarkWhite,
      borderBottomRightRadius: 10,
      borderBottomLeftRadius: 10,
      borderTopRightRadius: 8,
      borderTopLeftRadius: 8,
      paddingLeft: 10,
      paddingRight: 20,
      paddingBottom: 10,
      paddingTop: 8,
      display: 'flex',
      flex: 1,
      flexDirection: 'column',
    }}>
      <div>
        <b><p style={{fontSize: 5.5}}>{name}</p></b>
        <p style={{fontSize: 5.5, color: Colors.TextGray}}><b>Name:</b> {value}</p>
        <p style={{fontSize: 5.5, color: Colors.TextGray}}><b>Types:</b> {clsName}</p>
      </div>
    </div>
  );
};
export default DetailContainer;
