import React from "react";
import {MarkerType} from "react-flow-renderer";
import {Colors, defaultStrokeWidth} from "../Common/Constants";
import NodeContainer from "../Components/NodeContainer";
import DetailContainer from "../Components/DetailContainer";
import { classToColor } from "../Common/Helpers";
import { EdgeText } from 'react-flow-renderer';

export const extractNode = (vertex) => {
  // Input:
  // {
  //   "name": "0xe3549f2ca84ddc8b07dd5d81a9931c35d9f6c25d",
  //   "class": {
  //      name: "Smart contract"
  //      value: "Swap Router, MetaMask"
  //    }
  // }
  const horizontalScaleByUID = 60;
  const verticalScaleByUID = 70;
  const uid = vertex.uid;

  return {
    id: uid.toString(),
    class: vertex.class.value,
    data: {
      container: (
        <NodeContainer text={vertex.name.slice(0, 15) + ' . . .'} bg={classToColor(vertex.class.name)} />
      ),
      detailContainer:  (
        <DetailContainer name={vertex.name} clsName={vertex.class.name || ''} value={vertex.class.value || ''}/>
      ),
    },
    type: "hiddenNode",
  }
}


export const colorByMethod = {
  "swap": '#3b888e' ,
  "transferFrom": '#58929c' ,
  "allowance": '#2c7a7f' ,
  "uniswapV3Swap": '#3b6d72' ,
  "transfer": '#3a7f86' ,
  "balanceOf": '#2aa597' ,
  "uniswapV3SwapCallback": '#467b7b' ,
  "token0": '#47c6ba' ,
  "token1": '#3b858b' ,
  "fee": '#51b9ba',
}


export const convertToEdge = (edgeFromAPI) => {
  console.log(edgeFromAPI);

  // Input:
  // {
  //   {
  //     "uid": 0,
  //     "gas_value": 0.012861840061104128,
  //     "method_name": "swap",
  //     "currency": "-",
  //     "vertex_from": {
  //     "uid": 0,
  //       "name": "0xe3549f2ca84ddc8b07dd5d81a9931c35d9f6c25d",
  //       "annotation": null
  //   },
  //     "vertex_to": {
  //     "uid": 1,
  //       "name": "0x881d40237659c251811cec9c364ef91dc08d300c",
  //       "annotation": "Swap Router, MetaMask"
  //   }
  // }

  // Output:
  // {id: uuidv4(), source: '2', target: '3'},
  const edgeFromUid = edgeFromAPI.vertex_from.uid.toString();
  const edgeToUid = edgeFromAPI.vertex_to.uid.toString();
  const method = edgeFromAPI.method_name;

  return {
    id: edgeFromAPI.uid,
    type: 'input',
    source: edgeFromUid,
    target: edgeToUid,
    // markerEnd: {
    //   type: MarkerType.ArrowClosed,
    // },
    animated: true,
    style: {
      strokeWidth: defaultStrokeWidth,
      stroke: colorByMethod[method] || '#5c5c5c',
    },

    label: ' ',
    labelBgPadding: [3, 3],

    data: {
      edgeFromAPI
    },

    _fromTo: edgeFromUid + edgeToUid,
  }
}
