/* eslint-disable default-case */
export const classToColor = (name) => {
    switch (name) {
        case 'Smart contract':
            return '#264653';
        case 'Token':
            return '#2a9d8f';
        case 'Exchanger':
            return '#e76f51';
        case 'User':
            return '#e9c46a';
        default:
            return '#f4a261';
    }
}