/* eslint-disable */
const HOST = 'https://p2p-pbs.herokuapp.com'


export const downloadVertexEdges = async (tx) => {
  const response = await fetch(HOST + `/tx/${tx}`,{
    headers: {
      'Access-Control-Allow-Origin':'*'
    }
  });
  const json = await response.json();
  console.log("Received ", json);
  return json;
}
