import { useCallback, useState } from "react";
import {useStore} from "effector-react";
import CheckboxList from "../CheckboxList/CheckboxList";
import { updateItemStates } from "./updateItemStates";
import styles from "./Tree.module.scss";
import {
  setDisplayedNodes,
  $nodes,
} from "../../Common/Storage";

export const CheckboxState = {
  UNCHECKED: 'unchecked',
  CHECKED: 'checked',
  INDETERMINATE: 'intermediate',
}

const Tree = ({items}) => {
  const defaultItemStates = items.map((i) => ({
    id: i.id,
    state: CheckboxState.CHECKED,
  }));
  const allNodes = useStore($nodes);

  const [itemStates, setItemStates] = useState(defaultItemStates);
  const getStateForId = useCallback(
    (id) => {
      return itemStates.find((i) => i.id === id).state;
    },
    [itemStates]
  );
  const clickHandler = useCallback((id) => {
    const newState = updateItemStates(itemStates, items, id);
    setItemStates(newState);
    const newNodes = [...allNodes];
    newState.forEach(el => {
      const elem = items.find((i) => i.id === el.id)
      if (elem.parentId === 0) return;
      if (newState.find((i) => i.id === el.id).state === 'checked') return;
      let i = newNodes.length;
      while (i--) {
        if (newNodes[i].class === elem.name) {
          newNodes.splice(i, 1);
        }
      }
    })
    setDisplayedNodes(newNodes);
  }, [items, itemStates, allNodes]);
  return <div className={styles.tree}><CheckboxList items={items} onClick={clickHandler} getStateForId={getStateForId} /></div>;
};

export default Tree;
